            "The first Marvin Gaye album credited as produced solely by the artist himself, What's Going On is a unified concept album consisting of nine songs, most of which lead into the next. It has also been categorized as a song cycle; the album ends on a reprise of the album's opening theme. The album is told from the point of view of a Vietnam War veteran returning to the country he had been fighting for, and seeing nothing but injustice, suffering and hatred.\n \nWhat's Going On was the first album on which Motown Records' main studio band, the group of session musicians known as the Funk Brothers, received an official credit. The album features introspective lyrics and socially-conscious themes about drug abuse, poverty, and the Vietnam War. What's Going On was both an immediate commercial and critical success and has endured as a classic of early-1970s soul. <a href=\"https://www.last.fm/music/Marvin+Gaye/What%27s+Going+On\">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.",
