            'In 1979 Joni Mitchell reflected, "The Blue album, there\'s hardly a dishonest note in the vocals. At that period of my life, I had no personal defenses. I felt like a cellophane wrapper on a pack of cigarettes. I felt like I had absolutely no secrets from the world and I couldn\'t pretend in my life to be strong. Or to be happy. But the advantage of it in the music was that there were no defenses there either." <a href="https://www.last.fm/music/Joni+Mitchell/Blue">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
