          published: '02 Feb 2012, 18:00',
          content:
            "Yankee Hotel Foxtrot is the fourth album by Chicago-based rock band Wilco. The album was completed in 2001, but Reprise Records, a Warner Music Group label, refused to release it. Wilco acquired the rights to the album when they left the label. In September 2001, Wilco streamed the entire album for free on their website. Wilco signed with Nonesuch Records (another Warner label) in November of that year, and the album was officially released on April 23, 2002.\n\nYankee Hotel Foxtrot was a critical and commercial success, and is their best selling album, with over 500,000 copies sold in the U.S. and topping the Pazz and Jop critics' poll for 2002. Critical success endured, and the album was widely listed among the greatest albums of the 2000s in many popular publications, including 3rd place in Rolling Stone's 100 Best Albums of the 2000s. It was Wilco's first album with drummer Glenn Kotche, and the last with multi-instrumentalist and songwriter Jay Bennett. <a href=\"https://www.last.fm/music/Wilco/Yankee+Hotel+Foxtrot\">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.",
