          published: '05 May 2021, 23:16',
          content:
            'My Turn is the second studio album by American rapper Lil Baby. It was released on February 28, 2020, by Capitol Records, Motown Records, Wolfpack Music Group, and Quality Control Music. The album features guest appearances from Gunna, 42 Dugg, Future, Lil Uzi Vert, Lil Wayne, Moneybagg Yo, Young Thug, and Rylo Rodriguez. The album was supported by five singles: "Woah", "Sum 2 Prove", "Emotionally Scarred", "All In", and "The Bigger Picture". The latter song received two Grammy nominations at the 2021 Grammy Awards.\n\nMy Turn received generally favorable reviews and debuted atop the US Billboard 200, earning 197,000 album-equivalent units, becoming Lil Baby\'s first US number-one album. The album has topped the chart for five weeks, making it one of the best performing albums of 2020. It also reached the top 10 in other countries, including Canada and the United Kingdom. The album was certified triple platinum in February 2021. A deluxe edition, featuring six additional tracks, was released on May 1, 2020. <a href="https://www.last.fm/music/Lil+Baby/My+Turn">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
