            'evermore is the ninth studio album by American singer-songwriter Taylor Swift. It was released on December 11, 2020, through Republic Records, less than five months after the singer\'s eighth studio album, Folklore. Evermore was a spontaneous product of Swift\'s extended collaboration with her Folklore co-producer Aaron Dessner, with whom she developed a creative chemistry and could not stop writing songs even after releasing the latter. <a href="https://www.last.fm/music/Taylor+Swift/evermore">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
