            'Magdalene is the second studio album by FKA Twigs. It was released on November 8, 2019, by Young Turks. It is her first body of work since her EP M3LL155X (2015), and first full-length record since LP1 (2014). Production was handled by FKA Twigs herself, with co-producers including Nicolas Jaar, Koreless, Jack Antonoff, Metro Boomin, Kenny Beats, Oneohtrix Point Never, Jeff Kleinman, Skrillex, ARCA, Sounwave, and Michael Uzowuru. <a href="https://www.last.fm/music/FKA+twigs/MAGDALENE">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
