          content:
            'YHLQMDLG (abbreviation of "Yo hago lo que me da la gana", Spanish for "I Do Whatever I Want") is the second solo studio album (third overall) by Puerto Rican rapper and singer Bad Bunny. It was released on February 29, 2020, by Rimas Entertainment. It features guest appearances from Daddy Yankee, Nesi, Yaviah, Ñengo Flow, Sech, Mora, Jowell & Randy, Anuel AA, Myke Towers, Kendo Kaponi, Arcángel, Duki and Pablo Chill-E. The album debuted at number two on the US Billboard 200, becoming the highest-charting all-Spanish album ever on the chart at the time.\n\nIt became Spotify\'s most streamed album globally of 2020. It received a nomination for Best Latin Pop or Urban Album at the 63rd Annual Grammy Awards.\n <a href="https://www.last.fm/music/Bad+Bunny/YHLQMDLG">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
