            'Sound of Silver is the second studio album by American rock band LCD Soundsystem. The album was released jointly through DFA and Capitol Records in the United States and EMI elsewhere, first on March 12, 2007, in the United Kingdom. Sound of Silver was produced by the DFA and recorded during 2006 at Long View Farm in North Brookfield, Massachusetts and DFA Studios in New York, New York.\n\nUpon release, Sound of Silver received acclaim from music critics, and it was later nominated for the Grammy Award for Best Electronic/Dance Album at the 50th Annual Grammy Awards. <a href="https://www.last.fm/music/LCD+Soundsystem/Sound+Of+Silver">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
