          content:
            'The Money Store is the debut studio album by the group Death Grips. It was officially released on April 24, 2012, but was leaked to YouTube on April 14 and made available on vinyl on April 21 to celebrate Record Store Day. It was announced along with a second album, which would be titled No Love Deep Web. It is the follow-up to their debut mixtape Exmilitary.\n\nThe album was first hinted at with the release of a music video for a track titled "Blackjack" on February 7, 2012. The album was later announced along with the release of another track on February 27, 2012, titled "Get Got". It was then announced that the group had signed to Epic Records and were scheduled to release two albums in 2012. On February 28, 2012, the group leaked both songs for free download. On March 2, 2012, a video surfaced on their official YouTube channel of the band practicing a new track to be released on The Money Store, titled "Lost Boys." The studio version was then posted to their official YouTube channel on March 13. It was later released for free download on their website. On March 27, they released the music video for the song "The Fever (Aye Aye)," followed by a free download. On April 10, the song "I\'ve Seen Footage" was released for free download on their official SoundCloud page. Pitchfork Media awarded the track their "Best New Music" designation. On April 14, the album was leaked, and the band uploaded a complete version to their YouTube channel and their SoundCloud account. The following day, Pitchfork Media posted "Hacker," the album\'s closer, and named it "Best New Music." On September 10, 2012, a song titled "@DeathGripz," named after their Twitter handle, was released as the final instalment of the Adult Swim Singles Program 2012. The group had stated earlier that it was an unreleased cut from The Money Store.\n\nIn interviews, Death Grips have said that much of their music is created by sampling. They have said that they carry video cameras and tape recorders wherever they go and use samples from their experiences. <a href="https://www.last.fm/music/Death+Grips/The+Money+Store">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
