          published: '01 Jan 2017, 20:17',
          content:
            "Spirit They're Gone, Spirit They've Vanished is the first collaborative studio album by Avey Tare (David Portner) and Panda Bear (Noah Lennox), released in August 2000. It was later retroactively classified as the debut album by experimental pop band Animal Collective. The album was first released as a CD on the band's own Animal label (now known as Paw Tracks) with only 2000 copies produced.\n\nIt was reissued as a double CD along with Danse Manatee in 2003 on FatCat Records, and later on limited edition vinyl through FatCat. Both UK and US pressings used DMM mastering.\n\nAll of the songs were written by Avey Tare (David Portner) from 1997 to 1999, except for \"Penny Dreadfuls\" which was written when he was 16 years old. The process was strongly inspired by his move from Baltimore to New York, which he described as a \"dark time\" with Brian Weitz (Geologist) being his only friend in the city. Portner and Panda Bear (Noah Lennox) recorded the acoustic guitar and the drums live onto a Tascam 48 eight-track reel-to-reel in Portner's bedroom in Maryland during the summer of 1999. The piano songs along with overdubs were recorded in Portner's parents' living room. Portner wanted the guitar to be \"jangled to create this fluttering feeling\".\n\nAn old Roland SH-2 synthesizer, which Weitz's brother found stored in a camp, was used for the bass sounds. The drums were played with brushes to emulate the albums Ocean Rain and Forever Changes. Avey Tare would dictate what he wanted the rhythm to be through beatboxing. Other sounds, such as the majority of \"Spirit They've Vanished\", were created through the use of feedback loops.\n\nFuture Animal Collective member Deakin (Josh Dibb) helped with the promotion and sent packages to record companies. Portner recalled, \"Southern Records called us back immediately and said ‘Is there something wrong with this? This music makes our dogs run out of the room’!\"\n\nThe original cover art was found by Avey Tare, who considered it fitting. The art was later discovered to be an illustration by Dorothy P. Lathrop from the book Stars To-Night: Verses New and Old for Boys and Girls by Sara Teasdale. The album was intended to be released under Avey Tare's name alone, but he was so impressed by Panda Bear's drumming that he added Panda's name on the front cover. This method of choosing monikers for Animal Collective's recorded output—naming themselves after who played on each respective album—would last until 2003's Campfire Songs.\n\nIncluded with the original release was an insert with the following story:\n\n\"Not long ago a young boy named Avey and his friend Panda Bear roamed the patch work which covers the land (some call it forest). They were looked upon by Pan and raised by fairies and the Angels of light who play deep within the Wood. 'Let's make the music of childhood,' Avey shouted one day as the two played. Panda thought this idea was a good one. 'I can use my magical Rythm  sticks and you can play your Sun harp,' he said. So the two began to create melodies and Panda brought rythms  from every direction while Avey sang. All of the songs were about wooden toys and invisible friends and filled with the light of the forest. This is the first group of songs they played.\"\n\nThe title is a quote uttered by Mr. Magoo in Mister Magoo's Christmas Carol.\n\nThere has been some confusion about the album's track listing. On the original, limited release, the untitled track #3 was not listed along with the other songs. Since the track listing gave no corresponding track numbers to the rest of the songs, it was incorrectly assumed that tracks #3-9 were actually titled as tracks #4-10 are here, and that \"Alvin Row\" had no title. This error appears in nearly every review of Spirit following its initial release in 2000. The 2003 FatCat reissue cleared up this misunderstanding by numbering the songs and leaving a blank space for track #3.\n\n\n <a href=\"https://www.last.fm/music/Animal+Collective/Spirit+They%27re+Gone,+Spirit+They%27ve+Vanished\">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.",
