          content:
            'good kid, m.A.A.d (my Angry Adolescence divided) city is Kendrick Lamar’s first major label debut, after his signing to Aftermath and Interscope, released on October 22nd of 2012. This is also his second overall album, following 2011’s Section.80.\n\nIt enjoyed universal acclaim by critics, with an example being a score of 91 on Metacritic. It debuted at #2 on the US Billboard 200, with first-week sales of an amazing 242,000. In the US, it has since sold 1.72 million copies, and has been certified x3 platinum by the RIAA, for combined sales, streams, and track-sales.\n\nOn Sep. 16, 2019, the album became the longest- charting hip-hop studio album in Billboard 200 history, passing The Eminem Show. As of Sep. 16, 2019, it has charted every week since its release except the chart dated Dec. 6, 2014.\n\nIt is supported by 5 singles, “The Recipe,” “Swimming Pools (Drank),” “Backseat Freestyle,” “Poetic Justice,” and “Bitch, Don’t Kill My Vibe.” It also contains features by various artists, with popular ones being Dr. Dre and Drake.\n\nThe deluxe version, shown here, contains the aforementioned “The Recipe,” “Black Boy Fly”, and “Now or Never”. <a href="https://www.last.fm/music/Kendrick+Lamar/good+kid,+m.A.A.d+city+(Deluxe)">Read more on Last.fm</a>. User-contributed text is available under the Creative Commons By-SA License; additional terms may apply.',
